import TableHeaderRowComp from 'components/public/TableHeaderRowComp'
import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { ProductSubList_ListAction } from '../../redux/action/ProductSubListAction'

const TableHeaderComp = () => {
    const state = useSelector(state => state.ProductSubListReducer)
    const dispatch = useDispatch()

    const handleOrder = (orderby) => {
      let order = 'desc'
      if(state.orderby == orderby && state.order == 'desc')
        order = 'asc'

      dispatch(ProductSubList_ListAction({orderby , order , page : 1}))
    } 

  return (
    <div className='custom-tablerow custom-tablehead'>
        <TableHeaderRowComp state={state} value={"id"} title={''} handleOrder={handleOrder} w="10" className={'sticky right-0'}/>
        <p className='w-32 bg-mainbg' title=''></p>
        <p className='w-80 bg-mainbg' title=''></p>
        <p className='w-80 bg-mainbg' title=''></p>

        <TableHeaderRowComp state={state} value={'quantity'} title={' '} w={32} handleOrder={handleOrder} />
        <TableHeaderRowComp state={state} value={'paid_quantity'} title={''} w={20} handleOrder={handleOrder} />
        <TableHeaderRowComp state={state} value={'size'} title={''} w={32} handleOrder={handleOrder} />
        <TableHeaderRowComp state={state} value={'color'} title={''} w={32} handleOrder={handleOrder} />
        <TableHeaderRowComp state={state} value={'price'} title={''} w={20} handleOrder={handleOrder} />
        <TableHeaderRowComp state={state} value={'old_price'} title={'  '} w={20} handleOrder={handleOrder} />

        <p className='w-20 bg-mainbg' title=''></p>
        <p className='w-20 bg-mainbg' title=''></p>
        <p className='w-20 bg-mainbg' title=''></p>

        <p className='w-40 bg-mainbg' title=' '> </p>
        <TableHeaderRowComp state={state} value={'updated_at'} title={' '} w={40} handleOrder={handleOrder} />
        <TableHeaderRowComp state={state} value={'id'} title={' '} w={40} handleOrder={handleOrder} />
        <p className='w-10 bg-mainbg' title=''></p>
    </div>
  )
}

export default TableHeaderComp