import { z } from "zod";


export const CreateProductValidation = z.object({
    title : z.string().min(1 , "   ").max(255 , "    ") , 
    description : z.string().max(255 , "    ").nullable(),
    serial : z.string().max(255 , "    ").nullable(),
    publish_date : z.string().regex(/^(0[1-9]|[12][0-9]|3[01])-(0[1-9]|1[0-2])-(202[4-9]|203[0-9]) (0[0-9]|1[0-9]|2[0-3]):([0-5][0-9])$/, {
        message: "   ",
      }).nullable() ,
    json : z.object({
        description: z.string().nullable().optional(),
        restore: z.string().nullable().optional(),
    }).nullable() , 
    products : z.array(z.object({
        color : z.string().min(1 , '  ').max(255 , "    "),
        size : z.string().min(1 , '  ').max(255 , "    "),
        price : z.number().min(1 , "   ").max(999999 , "     "),
        old_price : z.number().min(0 , '   ').max(999999 , "     ").nullable(),
        quantity : z.number().min(0 , '   ').max(65535 , "     ").nullable(),
    }))
})


export const UpdateProductValidation = z.object({
    title : z.string().min(1 , "   ").max(255 , "    ") , 
    description : z.string().max(255 , "    ").nullable(),
    serial : z.string().max(255 , "    ").nullable(),
    publish_date : z.string().regex(/^(0[1-9]|[12][0-9]|3[01])-(0[1-9]|1[0-2])-(202[4-9]|203[0-9]) (0[0-9]|1[0-9]|2[0-3]):([0-5][0-9])$/, {
        message: "   ",
      }).nullable() ,
    json : z.object({
        description: z.string().nullable().optional(),
        restore: z.string().nullable().optional(),
    }).nullable()
})


export const SubProductValidation = z.object({
    color : z.string().min(1 , '  ').max(255 , "    "),
    size : z.string().min(1 , '  ').max(255 , "    "),
    price : z.number().min(1 , "   ").max(999999 , "     "),
    old_price : z.number().min(0 , '   ').max(999999 , "     ").nullable(),
    quantity : z.number().min(0 , '   ').max(65535 , "     ").nullable(),
})