import { z } from "zod";


export const  StoreAddressCreateValidation = z.object({
    address : z.string().min(1 , '   ').max(255 , '    ') ,
    json : z.object({
        phone : z.string().min(1 , '     ').max(255 , '    ') ,
        whatsapp : z.string().max(255 , '    ') ,
        email : z.string().max(255 , '    ') ,
        work : z.string().max(500 , '    ') ,
    })
})