import TableHeaderRowComp from 'components/public/TableHeaderRowComp'
import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Coupon_ListAction } from '../../redux/action/CouponAction'

const TableHeaderComp = () => {
    const state = useSelector(state => state.CouponReducer)
    const dispatch = useDispatch()

    const handleOrder = orderby => {
        let desc = state.order == 'desc' ? true : false

        if(state.orderby == orderby)
            desc = !desc

        dispatch(Coupon_ListAction({orderby , order : desc ? "desc" : "asc"}))
    }

  return (
    <div className='custom-tablerow bg-mainbg'>
        <TableHeaderRowComp w={10} title={''} value={'id'} className={'sticky right-0 justify-center'} handleOrder={handleOrder} state={state} />
        <p className='justify-center w-40'></p>
        <TableHeaderRowComp w={20} title={''} className={'justify-center'} value={'value'} handleOrder={handleOrder} state={state} />
        <TableHeaderRowComp w={40} title={' '} className={'justify-center'} value={'paid'} handleOrder={handleOrder} state={state} />
        <TableHeaderRowComp w={40} title={' '} className={'justify-center'} value={'is_used'} handleOrder={handleOrder} state={state} />
        <TableHeaderRowComp w={40} title={'  '} className={'justify-center'} value={'expire_date'} handleOrder={handleOrder} state={state} />
        <TableHeaderRowComp w={40} title={' '} className={'justify-center'} value={'updated_at'} handleOrder={handleOrder} state={state} />
        <TableHeaderRowComp w={40} title={' '} className={'justify-center'} value={'created_at'} handleOrder={handleOrder} state={state} />
        <p className='w-10 justify-center'></p>
    </div>
  )
}

export default TableHeaderComp