import { z } from "zod";


const validation = {
    name : z.string().min(1 , '   ').max(255 , "    ") ,
    email : z.string().max(255 , "    ") ,
    phone : z.string().max(11 , "   ").min(1, "   ")
    .refine(value => {
        const phoneRegex = /^01\d{9}$/;
        return phoneRegex.test(value);
    }, {
        message: "    "
    }) ,
}

export const CreateUserValidation = z.object({
    name : validation.name ,
    email : validation.email ,
    phone : validation.phone ,
    address : z.string().max(255 , "    ").optional().nullable() ,
    json : z.object({
        company : z.string().max(255 , "   ") ,
        city : z.string().max(255 , "   ").optional() ,
        governorate : z.string().max(255 , "   ").optional() ,
        country : z.string().max(255 , "   ") ,
        postal_code : z.string().max(255 , "   ") ,
    }) ,
    default : z.boolean() ,
}).refine(data => !(data.address && !data.json.city) , {
    message : "         " , 
    path:['json' , 'city']
}).refine(data => !(data.address && !data.json.governorate) , {
    message : "         " , 
    path:['json' , 'governorate']
})


export const UpdateUserValidation = z.object({
    name : validation.name ,
    email : validation.email.nullable() ,
    phone : validation.phone ,
})


export const UserAddressesValidation = z.object({
    address : z.string().max(255 , "   ").min(1 , "  ") ,
    json : z.object({
        name : z.string().max(255 , "   ") ,
        company : z.string().max(255 , "   ") ,
        city : z.string().max(255 , "   ").min(1 , '  ') ,
        governorate : z.string().max(255 , "   ").min(1 , "  ") ,
        country : z.string().max(255 , "   ") ,
        postal_code : z.string().max(255 , "   ") ,
        phone : z.string().max(255 , "   ") 
    }),
    default : z.boolean()
})



/**
 * create user inside for order page
 */
export const OrderPageCreateUserValidation = z.object({
    name : validation.name ,
    email : validation.email ,
    phone : validation.phone ,
    address : z.string().max(255 , "    ").optional().nullable() ,
    json : z.object({
        city : z.string().max(255 , "   ").optional() ,
        governorate : z.string().max(255 , "   ").optional() ,
    }) ,
}).refine(data => !(data.address && !data.json.city) , {
    message : "         " , 
    path:['json' , 'city']
}).refine(data => !(data.address && !data.json.governorate) , {
    message : "         " , 
    path:['json' , 'governorate']
})