import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { ProductList_SearchAction } from '../../redux/action/ProductListAction'
import TableHeaderRowComp from '../public/TableHeaderRowComp'

const TableHeaderComp = () => {
    const state = useSelector(state => state.ProductListReducer)
    const dispatch = useDispatch()
    const [order , setOrder] = useState(null)

    const handleOrder = (orderby) => {
        let is_desc = 'desc'
        
        // check if input is multi-clicked makes its order ascending
        if(orderby == order && state.order == 'desc')
          is_desc = 'asc'
    
        setOrder(orderby)
        dispatch(ProductList_SearchAction({orderby , order : is_desc}))
      }

  return (
    <div className='custom-tablerow custom-tablehead'>
        <TableHeaderRowComp state={state} value={"id"} title={''} handleOrder={handleOrder} w="10" className={'sticky right-0'}/>
        <p className='w-32 bg-mainbg' title=''></p>
        <TableHeaderRowComp state={state} value={'title'} title={''} w={80} handleOrder={handleOrder} />
        <TableHeaderRowComp state={state} value={'description'} title={''} w={80} handleOrder={handleOrder} />
        <p className='w-20 bg-mainbg' title=''></p>
        <TableHeaderRowComp state={state} value={'ratting'} title={''} w={20} handleOrder={handleOrder} />
        <TableHeaderRowComp state={state} value={'views'} title={''} w={20} handleOrder={handleOrder} />
        <TableHeaderRowComp state={state} value={'reviews'} title={''} w={20} handleOrder={handleOrder} />
        <TableHeaderRowComp state={state} value={'publish_date'} title={' '} w={40} handleOrder={handleOrder} />
        <TableHeaderRowComp state={state} value={'updated_at'} title={' '} w={40} handleOrder={handleOrder} />
        <TableHeaderRowComp state={state} value={'id'} title={' '} w={40} handleOrder={handleOrder} />
        <p className='w-10 bg-mainbg' title=''></p>
    </div>
  )
}

export default TableHeaderComp