

export const OrderStatus = (req = null) => {
    const status = {    
        pending : "  " , 
        ready : " " , 
        preparing : " " , 
        success : "" , 
        canceled : "" 
    }

    return req ? status[req] : " "
}


/**
 * formating orders 
 */
export const formattingOrderStatus = (val) => {
    let value = ""
    switch(val){
        case "pending" :
            value = "  " 
            break;
        case "ready" :
            value = " " 
            break;
        case "preparing" :
            value = " " 
            break;
        case "success" :
            value = "" 
            break;
        case "canceled" :
            value = "" 
            break;
        case "canceled without refund" :
            value = "     "
            break;
    }

    return value
} 